import styled, {css} from "styled-components";

const CustomToggleSwitch = styled.div`
    position: absolute;
    width: 100px;
    height: 100px;
    right: 4rem;
    bottom: -2.5rem;
`

const CustomToggleLabel = styled.label`
    ${({theme}) => {

        const {colors} = theme;

        return css`
            position: absolute;
            width: 80%;
            height: 40px;
            background-color: inherit;
            border-radius: 50px;
            border: 1px solid ${colors.textColor};
            cursor: pointer;
        `
    }}
`

const CustomToggleInput = styled.input.attrs({ type: "checkbox" })`
    position: absolute;
    display:none;
`

const CustomSpanSlider = styled.span`
    position: absolute;
    width: 50%;
    height: 50%;
    border-radius: 50px;
    transition: 0.3s;

    &:before{
        content: "";
        position: absolute;
        top: 7px;
        left: 5px;
        width: 30px;
        height: 27px;
        border-radius: 50%;
        box-shadow: inset 15px -1px 0px 0px ${({theme}) => theme.colors.textColor};
        background-color: inherit;
        transition: 0.3s;
    }

    &:after{
        content: "DARK";
        width: 100px;
        height: 42px;
        display: table-cell;
        vertical-align: middle;
        text-align: end;
        padding-left: 37px;
        color: ${({theme}) => theme.colors.textColor};
        font-size: 10px;
    }

    ${CustomToggleInput}:checked + &:before{
        transform: translateX(39px);
        background-color: ${({theme}) => theme.colors.textColor};
        box-shadow: none;
        height: 30px;
        top: 5px;
    }

    ${CustomToggleInput}:checked + &:after{
        content: "LIGHT";
        padding-left: 0;
    }
    
`

export const StyledToggle = {
    CustomToggleSwitch,
    CustomToggleLabel,
    CustomToggleInput,
    CustomSpanSlider
}