import React, {useCallback} from "react";
import { ThemeEnums, themeMode } from "../../recoil/Theme";
import {
    useRecoilState
  } from 'recoil';
import { StyledToggle } from "./StyledToggle";



const {CustomToggleSwitch, CustomToggleLabel, CustomToggleInput, CustomSpanSlider} = StyledToggle;

const Toggle = () => {

    const [themeType, setThemeType] = useRecoilState<ThemeEnums>(themeMode);
    
        const modeTest = useCallback(():void => {


                if(themeType === ThemeEnums.DARK) {
                    localStorage.setItem('theme', ThemeEnums.LIGHT.toString());
                    setThemeType(ThemeEnums.LIGHT);
                    return
                }
        
                localStorage.setItem('theme', ThemeEnums.DARK.toString());
                setThemeType(ThemeEnums.DARK);
    
            
    
        },[setThemeType, themeType, ThemeEnums.DARK, ThemeEnums.LIGHT]);


    

    return(
        <CustomToggleSwitch>
            <CustomToggleLabel>
                <CustomToggleInput onChange={ modeTest }></CustomToggleInput>
                <CustomSpanSlider> 
                </CustomSpanSlider>
            </CustomToggleLabel>
        </CustomToggleSwitch>
    )
}

export default Toggle;