import React from "react";
import { DarkTheme, LightTheme } from "../../styles/them";
import styled, {ThemeProvider} from "styled-components";
import GlobalStyle from "../../styles/globalStyle";
import Header from "./Header";
import { useRecoilValue, RecoilRoot } from "recoil";
import { ThemeEnums, themeMode } from "../../recoil/Theme";

// #ece8dd

type Props = {
    children?: React.ReactNode | React.ReactNode[];
};

const CustomLayout = styled.div`
    height: auto;
    overflow: hidden;
`

const Layout = ({children}:Props) => {

    const theme =  useRecoilValue(themeMode);

    console.log(theme);
    if (typeof window === 'undefined') console.log('Window is not there')

    return(
        <ThemeProvider theme={  theme === ThemeEnums.LIGHT ? LightTheme : DarkTheme }>
        <GlobalStyle />
            <CustomLayout>
                <Header title="SUWON's BLOG"></Header>
                {children}
            </CustomLayout>
            </ThemeProvider>
    )
}

export default Layout;
