import {atom} from "recoil";

export enum ThemeEnums {
    LIGHT = 0,
    DARK = 1,
};

const isBrowser = typeof window !== "undefined"

export const getTheme = ():ThemeEnums => {
    var stotage;
    if(isBrowser) {
        stotage = localStorage.getItem('theme');
        const theme: number = Number(stotage);

        if(theme === ThemeEnums.DARK) {
            return ThemeEnums.DARK;
        }
    }

    return ThemeEnums.LIGHT;
    
}

export const themeMode = atom<ThemeEnums>({
    key: 'themeMode',
    default: getTheme(),
})